import ReactDOM from "react-dom";
import { Alert, Button } from "uiw";

class Demo extends React.Component {
  constructor() {
    super();
    this.state = {
      visible: false,
    };
  }
  onClick() {
    this.setState({ visible: !this.statevisible });
  }
  onClosed() {
    this.setState({ visible: false });
  }
  render() {
    return (
      <div>
        <Alert
          icon="delete"
          isOpen={this.state.visible}
          confirmText=""
          type="danger"
          onConfirm={() => {
            console.log(", Promise");
            return new Promise((resolve, reject) => {
              const random = Math.random();
              console.log(
                " 0.5  resolve   reject  catch ",
                random,
                random > 0.5
              );
              setTimeout(random > 0.5 ? resolve : reject, 3000);
            }).catch(() => {
              // 
              // throw new Error();
            });
          }}
          onClosed={this.onClosed.bind(this, "visible")}
        >
           <b>Promise</b>{" "}
          
          <br />
          <br />
           <b>0.5</b>  <b>resolve</b> 
           <b>reject</b>  <b>catch</b> 
        </Alert>
        <Button onClick={this.onClick.bind(this, "visible")}>
          
        </Button>
      </div>
    );
  }
}
export default Demo;
