import ReactDOM from "react-dom";
import { Alert, ButtonGroup, Button } from "uiw";

class Demo extends React.Component {
  constructor() {
    super();
    this.state = {
      visible: false,
    };
  }
  onClick(type) {
    this.setState({ visible: !this.state.visible });
  }
  onClosed(type) {
    this.setState({ visible: false });
  }
  render() {
    return (
      <div>
        <Alert
          isOpen={this.state.visible}
          type="danger"
          useButton={false}
          maskClosable={true}
          onClose={this.onClosed.bind(this)}
          // onClosed={this.onClosed.bind(this)}
          content=""
        >
           <b></b>  <b></b>
           <del>onConfirm</del>  
          <del>onCancel</del> 
          <br />
          <br />
          <div className="w-alert-footer">
            <Button type="danger" onClick={this.onClosed.bind(this)}>
              
            </Button>
            <Button onClick={this.onClosed.bind(this)}></Button>
          </div>
        </Alert>
        <ButtonGroup>
          <Button onClick={this.onClick.bind(this)}></Button>
        </ButtonGroup>
      </div>
    );
  }
}
export default Demo;
